import React from 'react';
import { Button } from '../../../common';

interface FormActionsProps {
  onSubmit: () => void;
  onPrint: () => void;
  onExportPDF?: () => void; //  PDF 
  isSubmitting: boolean;
}

const FormActions: React.FC<FormActionsProps> = ({
  onSubmit,
  onPrint,
  onExportPDF,
  isSubmitting
}) => {
  return (
    <div className="form-actions">
      <Button
        type="button"
        variant="secondary"
        onClick={onPrint}
        disabled={isSubmitting}
      >
        
      </Button>
      {onExportPDF && (
        <Button
          type="button"
          variant="secondary"
          onClick={onExportPDF}
          disabled={isSubmitting}
        >
           PDF
        </Button>
      )}
      <Button
        type="submit"
        variant="primary"
        onClick={onSubmit}
        disabled={isSubmitting}
      >
        {isSubmitting ? '...' : ''}
      </Button>
    </div>
  );
};

export default FormActions;
