import React, { useEffect } from 'react';
import imageCompression from 'browser-image-compression';

interface PhotoUploadProps {
  //  props  URL  File 
  onPhotoChange: (photoDataURL: string | null, compressedFile: File | null) => void;
  photoDataURL: string | null;
}

const PhotoUpload: React.FC<PhotoUploadProps> = ({ onPhotoChange, photoDataURL }) => {
  //  useEffect Hook  Object URL 
  useEffect(() => {
    //  effect  cleanup function
    //  component unmount  photoDataURL 
    return () => {
      if (photoDataURL) {
        //  Object URL
        URL.revokeObjectURL(photoDataURL);
        console.log(` Object URL`);
      }
    };
  }, [photoDataURL]); //  effect  photoDataURL

  const handlePhotoUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];

    if (file) {
      console.log(`: ${(file.size / 1024 / 1024).toFixed(2)} MB`);

      // 
      const options = {
        maxSizeMB: 0.8,         //  0.8MB
        maxWidthOrHeight: 1280, //  1280px
        useWebWorker: true,     //  UI 
        fileType: 'image/jpeg', //  JPG
        initialQuality: 0.7,    // 
        onProgress: (p: number) => {
          console.log(`: ${p}%`);
        },
      };

      try {
        console.log('...');
        const compressedFile = await imageCompression(file, options);
        console.log(`: ${(compressedFile.size / 1024 / 1024).toFixed(2)} MB`);

        //  Object URL 
        const objectURL = URL.createObjectURL(compressedFile);
        
        //  URL  File 
        onPhotoChange(objectURL, compressedFile);

      } catch (error) {
        console.error(':', error);
        alert('');
        onPhotoChange(null, null);
      }
    } else {
      // 
      onPhotoChange(null, null);
    }
  };

  return (
    <div className="photo-upload">
      <h3></h3>
      <div className="photo-actions">
        <input
          type="file"
          id="photo-upload-input"
          accept="image/*"
          onChange={handlePhotoUpload}
          capture="environment"
          className="photo-input"
          style={{ display: 'none' }}
        />
        <label htmlFor="photo-upload-input" className="camera-button">
          
        </label>
      </div>
      {photoDataURL && (
        <div className="photo-preview">
          <img src={photoDataURL} alt="" />
        </div>
      )}
    </div>
  );
};

export default PhotoUpload;
